import React from "react";
import ReactDOM from "react-dom";
import { createBrowserHistory } from "history";
import { Router, Route, Switch } from "react-router-dom";
import * as serviceWorker from './serviceWorker';

import "assets/scss/material-kit-react.scss?v=1.9.0";

// pages for this product
import Master from "views/masterComponents/Components.js";
import ProfilePage from "views/ProfilePage/ProfilePage.js";
import LoginPage from "views/LoginPage/LoginPage.js";
import HomePage from "views/Home/Home_Views.js";
import LittleThoughts from "views/LittleThoughts/LitleThoughtsPage.js";
// import bg_body from "assets/img/bg7.jpg"
import bg_body from "assets/img/magical.jpg"

var hist = createBrowserHistory();

ReactDOM.render(
  <div
    style={{
      backgroundImage: `url(${bg_body})`,
      backgroundAttachment: "fixed",
      backgroundPosition: "top",
      backgroundSize: "cover"
    }}>

    <Router history={hist} >
      <Switch>
        <Route path="/little_thoughts-page" component={LittleThoughts} />
        <Route path="/master-page" component={Master} />
        <Route path="/profile-page" component={ProfilePage} />
        <Route path="/login-page" component={LoginPage} />
        <Route path="/" component={HomePage} />
      </Switch>
    </Router>
  </div>,
  document.getElementById("root")
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
// serviceWorker.unregister();

serviceWorker.register({
  onUpdate: registration => {
    const waitingServiceWorker = registration.waiting

    if (waitingServiceWorker) {
      waitingServiceWorker.addEventListener("statechange", event => {
        if (event.target.state === "activated") {
          if (
            window.confirm(
              `App new version 1.0.0 ready. Please reload to update.`
            )
          ) {
            window.location.reload()
          }
        }
      })
      waitingServiceWorker.postMessage({ type: "SKIP_WAITING" })
    }
  },
})
